const clickOutside = {
    bind(el, binding, vNode) {
        // Provided expression must evaluate to a function.
        if (typeof binding.value !== 'function') {
            const compName = vNode.context.name;
            let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`;
            if (compName) {
                warn += `Found in component '${compName}'`;
            }
            console.warn(warn); // eslint-disable-line no-console
        }
        // Define Handler and cache it on the element
        const bubble = binding.modifiers.bubble;
        const handler = (e) => {
            if (bubble || (!el.contains(e.target) && el !== e.target)) {
                binding.value(e);
            }
        };
        el.vueClickOutsideElement = handler; // eslint-disable-line no-param-reassign
        // add Event Listeners
        document.addEventListener('click', handler);
    },
    unbind(el) {
        // Remove Event Listeners
        document.removeEventListener('click', el.vueClickOutsideElement);
        el.vueClickOutsideElement = null; // eslint-disable-line no-param-reassign
    },
    install(Vue) {
        Vue.directive('click-outside', {
            bind: this.bind,
            unbind: this.unbind,
        });
    },
};

export default clickOutside;
