<template>
  <div class="date-picker" v-click-outside="close">
    <div class="date-picker-values">
      <span class="label period-select-label">Kies datum</span>
      <div class="date-picker-field" @click="toggleVisibility">
        <i class="material-icons picker-icon">date_range</i>
        <span class="date-readable">{{ readable }}</span>
      </div>
    </div>
    <period-select :selected-day="dates.selected"></period-select>
    <!-- Calendar -->
    <div v-if="visible" class="date-picker-modal">
      <div class="date-picker-calendar">
        <header class="date-picker-header">
                    <span class="control">
                        <a @click="setCalendarDate(previousYear)" class="previous-year" v-if="allowPreviousYear">
                            <i class="material-icons">fast_rewind</i>
                        </a>
                        <a @click="setCalendarDate(previousMonth)" class="previous-month" v-if="allowPreviousMonth">
                            <i class="material-icons">chevron_left</i>
                        </a>
                    </span>
          <span class="date-picker-title">
                        <span class="year">{{ year }}</span>
                        <span class="month">{{ month }}</span>
                    </span>
          <span class="control">
                        <a @click="setCalendarDate(nextMonth)" class="next-month">
                            <i class="material-icons">chevron_right</i>
                        </a>
                        <a @click="setCalendarDate(nextYear)" class="next-year">
                            <i class="material-icons">fast_forward</i>
                        </a>
                    </span>
        </header>
        <section class="date-picker-body">
          <header class="weekday-grid">
            <div v-for="(dayOfTheWeek, index) in daysOfTheWeek"
                 class="weekday"
                 :key="index">
              {{ dayOfTheWeek }}
            </div>
          </header>
          <div class="calendar-grid">
            <calendar-day v-for="(day, index) in days"
                          :day="day"
                          :key="index"
                          :date="day.date"
                          @click.native.stop="selectDate(day, true)">
            </calendar-day>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    format,
    subMonths,
    subYears,
    differenceInCalendarDays,
    startOfWeek,
    endOfWeek,
    startOfMonth,
    endOfMonth,
    isDate,
    isSameDay,
    isSameMonth,
    isSameYear,
    isMonday,
    isFriday,
    isWeekend,
    isPast,
    addMonths,
    addYears,
    addDays,
    setDay
  } from 'date-fns'

  import ClickOutside from './directives/click-outside'
  import calendarDay from './components/Day'
  import periodSelect from './components/Period'

  const nlLocale = require('../node_modules/date-fns/locale/nl')
  const frLocale = require('../node_modules/date-fns/locale/fr')
  const enLocale = require('../node_modules/date-fns/locale/en')

  export default {
    name: 'DatePicker',
    props: {
      callBack: {
        type: Function,
        required: false,
        default () {}
      },
      locale: {
        type: String,
        required: false,
        default: 'nl'
      },
      inputName: {
        type: String,
        default: 'date'
      },
      defaultToToday: {
        type: Boolean,
        required: false,
        default: false
      },
      selectedDate: {
        type: [Date, String],
        required: false
      },
      selectedTime: {
        type: String,
        default: '10:00',
        required: false
      },
      showTimePicker: {
        type: Boolean,
        default: false
      },
      minDate: {
        type: String
      },
      maxDate: {
        type: String
      },
      minTime: {
        type: String,
        required: false,
        default: '09:00'
      },
      maxTime: {
        type: String,
        required: false,
        default: '14:00'
      },
      timeInterval: {
        type: Number,
        required: false,
        default: 60
      },
      allowsPastDates: {
        type: Boolean,
        required: false,
        default: false
      }
    },
    components: {
      periodSelect,
      calendarDay
    },
    directives: {
      ClickOutside
    },
    data () {
      return {
        visible: false,
        dates: {
          selected: null,
          calendar: {
            date: new Date(),
            start: null,
            end: null
          }
        },
        formats: {
          day: 'D',
          year: 'YYYY',
          month: 'MMMM',
          readable: 'dddd, D MMMM (YYYY)',
          sql: 'YYYY-MM-DD',
          hour: 'HH:mm'
        },
        weekMask: [
          1,
          2,
          3,
          4,
          5,
          6,
          0
        ]
      }
    },
    mounted () {
      this.selectDate({date: this.selectedDate})
    },
    watch: {
      selectedDate (val) {
        if (!val) {
          this.clearDate(val)
        }
      }
    },
    computed: {
      selectedLocale() {
        if (this.locale === 'fr') {
          return frLocale
        }
        if (this.locale === 'en') {
          return enLocale
        }
        return nlLocale
      },
      days () {
        let days = []
        let n = 0

        // We will start adding days to the array
        // until we have a full calendar month
        while (n < this.amountOfCalendarMonthDays) {
          let date = addDays(this.dates.calendar.start, n)

          days.push({
            is_today: isSameDay(date, new Date()),
            is_weekend: isWeekend(date, {weekStartsOn: 1}),
            is_different_month: !isSameMonth(this.dates.calendar.date, date),
            is_selected: isSameDay(this.dates.selected, date),
            is_disabled: (!isMonday(date) && !isFriday(date)) || (isPast(date) && !this.allowsPastDates),
            date: date,
            readable_date: format(date, 'DD')
          })
          n++
        }

        return days
      },
      amountOfCalendarMonthDays () {
        return (
          differenceInCalendarDays(
            this.dates.calendar.end,
            this.dates.calendar.start
          ) + 1
        )
      },
      month () {
        return format(this.dates.calendar.date, this.formats.month, {locale: this.selectedLocale})
      },
      year () {
        return format(this.dates.calendar.date, this.formats.year)
      },
      nextMonth () {
        return addMonths(this.dates.calendar.date, 1)
      },
      nextYear () {
        return addYears(this.dates.calendar.date, 1)
      },
      previousMonth () {
        return subMonths(this.dates.calendar.date, 1)
      },
      previousYear () {
        return subYears(this.dates.calendar.date, 1)
      },
      allowPreviousMonth () {
        if (this.allowsPastDates) {
          return true
        }
        if (isSameMonth(new Date(), this.previousMonth)) {
          return true;
        }
        return !isPast(this.previousMonth);
      },
      allowPreviousYear () {
        if (this.allowsPastDates) {
          return true
        }
        if (isSameYear(new Date(), this.previousYear)) {
          return true;
        }
        return !isPast(this.previousYear);
      },
      readable () {
        if (!isDate(this.dates.selected)) {
          return 'kies een datum'
        }
        return format(this.dates.selected, this.formats.readable,
          {locale: this.selectedLocale})
      },
      time () {
        return format(this.dates.selected, this.formats.hour)
      },
      daysOfTheWeek () {
        const mask = {
          0: format(setDay(this.dates.calendar.date, 0), 'dd', {locale: this.selectedLocale}),
          1: format(setDay(this.dates.calendar.date, 1), 'dd', {locale: this.selectedLocale}),
          2: format(setDay(this.dates.calendar.date, 2), 'dd', {locale: this.selectedLocale}),
          3: format(setDay(this.dates.calendar.date, 3), 'dd', {locale: this.selectedLocale}),
          4: format(setDay(this.dates.calendar.date, 4), 'dd', {locale: this.selectedLocale}),
          5: format(setDay(this.dates.calendar.date, 5), 'dd', {locale: this.selectedLocale}),
          6: format(setDay(this.dates.calendar.date, 6), 'dd', {locale: this.selectedLocale})
        }

        return this.weekMask.map(day => mask[day])
      }
    },

    methods: {
      selectDate (day, performCallback = false) {
        // Do not allow disabled days
        if (day.is_disabled) {
          return
        }

        // Do not allow past dates if prop says so
        if (!this.allowsPastDates && isPast(day.date)) {
          return
        }

        // If there is a date as String passed
        if (day && typeof day.date === 'string') {
          let date = new Date(day.date)
          this.dates.selected = date
          this.setCalendarDate(date)
        }
        // If there is a date as Date passed
        if (day && typeof day.date === 'object') {
          this.dates.selected = day.date
          this.setCalendarDate(day.date)
        }

        // If date is undefined, default to now
        if (day && !day.date) {
          let date = new Date()
          this.dates.selected = null
          this.setCalendarDate(date)
        }

        if (performCallback) {
          this.callBack({
            date: format(day.date, this.formats.sql),
            input: this.inputName
          })
        }
      },
      setCalendarDate (date) {
        this.dates.calendar = {
          date: date,
          start: startOfWeek(startOfMonth(date), {weekStartsOn: 1}),
          end: endOfWeek(endOfMonth(date), {weekStartsOn: 1})
        }
      },
      clearDate () {
        this.selectDate({ date: null })
      },
      close () {
        this.toggleVisibility(false)
      },
      toggleVisibility (v) {
        this.visible = v === false ? false : !this.visible
      },
    }
  }
</script>

<style lang="scss">
  $color: #717171;
  $header-background: #f9f9f9;
  $background: #fff;
  $txt-yellow: #303030;
  $txt-faint-gray: #a1a1a1;
  $box-shadow: 0 2px 6px rgba(68, 68, 68, 0.24);
  $highlight: #6990ff;
  $font-size: .9rem;

  @mixin box {
    box-shadow: $box-shadow;
    border: none;
    border-bottom: 2px solid whitesmoke;
    padding: 32px;
    margin-bottom: 8px;
    background-color: #fff;
  }

  $white-theme: (
    'background': #fff,
    'color': $color,
    'border': whitesmoke,
    'title-color': $color,
    'link-color': $txt-faint-gray,
    'selected-color': #fff,
    'selected-background': $txt-yellow,
    'not-current-month-color': hsl(0, 0%, 87%),
    'header-background': $header-background,
    'header-days-background': $background,
    'header-days-color': #b1b1b1,
    'day-color-selected': #fff,
    'day-color-hover': $color,
    'day-background-hover': #eaeaea,
    'day-background-selected': $highlight,
    'day-background-today': #e6e6e6,
    'day-background-disabled': whitesmoke,
    'day-color-today': $color,
    'day-color-disabled': lighten($color, 30%),
    'day-border-radius': 2px,
    'time-border-radius': 2px
  );
  $zaz-theme: (
    'background': #fff,
    'color': $color,
    'border': whitesmoke,
    'title-color': $color,
    'link-color': $txt-faint-gray,
    'selected-color': #fff,
    'selected-background': $txt-yellow,
    'not-current-month-color': hsl(0, 0%, 87%),
    'header-background': $header-background,
    'header-days-background': $background,
    'header-days-color': #b1b1b1,
    'day-color-selected': #fff,
    'day-color-hover': $color,
    'day-background-hover': #eaeaea,
    'day-background-selected': $highlight,
    'day-background-today': #e6e6e6,
    'day-background-disabled': whitesmoke,
    'day-color-today': $color,
    'day-color-disabled': lighten($color, 30%),
    'day-border-radius': 2px,
    'time-border-radius': 2px
  );

  $map: $zaz-theme;

  .date-picker {
    box-sizing: border-box;
    display: flex;
    font-family: Ubuntu, Helvetica, Arial, sans-serif;

    .label {
      font-weight: bold;
      padding-bottom: .4rem;
      display: block;
      font-size: $font-size;
    }

    .date-readable {
      cursor: pointer;
      font-size: $font-size;
    }

    .date-picker-title {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      line-height: 1.6rem;

      .month {
        font-weight: 600;
        font-size: 1.3rem;
      }

      .year {
        font-weight: 400;
        color: $txt-faint-gray;
        font-size: 1rem;
      }
    }

    .weekday-grid {
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      grid-gap: 2px;

      > div {
        height: 3rem;
        text-align: center;
      }
    }

    .calendar-grid {
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      grid-auto-flow: dense;
      grid-gap: 2px;
    }
  }

  .date-picker-values {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    i {
      margin-right: .6rem;
      font-size: 16px;
    }
  }

  .date-picker-field {
    margin-right: 1rem;
    display: flex;
    align-items: center;
    padding: .45rem 0.65rem;
    border-radius: 3px;
    background-color: white;
    font-family: Ubuntu, Helvetica, Arial, sans-serif;
    font-size: $font-size;
    border: 1px solid rgb(169, 169, 169);
    cursor: pointer;
  }

  .date-picker-field-separator {
    margin-right: 1rem;
    display: flex;
    align-items: center;
    width: 1rem;
    justify-content: center;
    color: #b9b9b9;
  }

  .date-picker-modal {
    @include box;
    padding: 0;
    z-index: 10;
    display: flex;
    justify-content: flex-start;
    align-self: baseline;
    margin-top: 1rem;
    align-items: stretch;
    position: absolute;
    top: 3rem;
  }

  header.date-picker-header {
    display: grid;
    grid-template-columns: 60px 1fr 60px;
    align-items: flex-end;
    padding: 1.2rem;
    background-color: map-get($map, 'header-background');

    a {
      color: map-get($map, 'link-color');
      display: flex;
      text-decoration: none;
      opacity: .3;
      transition: opacity 300ms linear 1000ms;
    }

    .date {
      font-weight: 500;
      color: map-get($map, 'title-color');
    }

    .control {
      display: flex;

      i {
        line-height: 0.8;
      }
    }

    &:hover {
      a {
        opacity: 1;
        transition: opacity 300ms linear 100ms;
        cursor: pointer;
      }
    }
  }

  div.date-picker-body {
    float: left;
  }

  div.date-picker-calendar {
    font-size: .9rem;
    outline: none;
    text-align: center;
    background-color: map-get($map, 'background');
    color: map-get($map, 'color');
    user-select: none;
    cursor: default;
    padding: 0;

    header.weekday-grid {
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      grid-gap: 2px;
      padding: 1rem 1rem 0 1rem;

      > .weekday {
        height: 3rem;
        text-align: center;
        border: none;
        color: map-get($map, 'header-days-color');
        background-color: map-get($map, 'header-days-background');
        font-weight: 500;
        cursor: default;
        align-items: center;
        justify-content: center;
        display: flex;
      }
    }

    .calendar-grid {
      float: left;
      height: 280px;
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      grid-auto-flow: dense;
      grid-gap: 2px;
      padding: 1rem;

      > .day {
        flex: 1;
        padding: 0.6rem 1rem;
        cursor: pointer;
        border-radius: map-get($map, 'day-border-radius');
        align-items: center;
        justify-content: center;
        display: flex;
        transition: background-color 120ms ease-in;

        &:hover {
          color: map-get($map, 'day-color-hover');
          background-color: map-get($map, 'day-background-hover');
        }

        &.not-current-month {
          color: map-get($map, 'not-current-month-color');
        }

        &.today {
          background: map-get($map, 'day-background-today');
          color: map-get($map, 'day-color-today');
        }

        &.selected {
          transition: background-color 50ms ease-out;
          background: map-get($map, 'day-background-selected');
          color: map-get($map, 'day-color-selected');
        }

        &.disabled {
          background: map-get($map, 'day-background-disabled');
          color: map-get($map, 'day-color-disabled');
          cursor: not-allowed;
        }
      }
    }

  }
</style>
