<template>
  <div class="period-picker">
    <span class="label period-select-label">Kies periode</span>
    <div class="period-select-container">
      <select name="booker_duration" id="booker_duration">
        <option v-for="period in filteredPeriods" :value="period.value" :key="period.value">{{ period.readable.nl }}</option>
      </select>
    </div>
  </div>
</template>
<script>
  import {
    isMonday,
    isFriday
  } from 'date-fns'

  export default {

    data () {
      return {
        periods: [
          {
            'value': 'weekend',
            'readable': {
              'nl': 'Weekend: vrijdag 17u tot maandag 10u'
            }
          },
          {
            'value': 'midweek',
            'readable': {
              'nl': 'Midweek: maandag 17u tot vrijdag 10u'
            }
          },
          {
            'value': 'week',
            'readable': {
              'nl': 'Week: maandag 17u tot maandag 10u'
            }
          },
          {
            'value': '2weeks',
            'readable': {
              'nl': '2 weken: maandag 17u tot maandag 10u'
            }
          }
        ],
        periodsStartingMonday: [
          'midweek',
          'week',
          '2weeks'
        ],
        periodsStartingFriday: [
          'weekend'
        ]
      }
    },
    props: {
      selectedDay: {
        required: false
      }
    },
    computed: {
      filteredPeriods () {
        let periods = this.periods

        if (this.selectedDay) {
          if (isFriday(this.selectedDay)) {
            periods = periods.filter((p) => {
              return this.periodsStartingFriday.includes(p.value)
            })
          }
          if (isMonday(this.selectedDay)) {
            periods = periods.filter((p) => {
              return this.periodsStartingMonday.includes(p.value)
            })
          }
        }
        return periods
      }
    },
    methods: {}
  }
</script>

<style lang="scss" type="scss">
  $font-size: .9rem;

.period-picker {
  margin-left: 2rem;

  select {
    padding: .4rem;
    border-radius: 3px;
    background-color: white;
    font-family: Ubuntu, Helvetica, Arial, sans-serif;
    font-size: $font-size;
  }
}
</style>
